import React from 'react';
import { graphql } from 'gatsby';
import Helmet from 'react-helmet';
import PropTypes from 'prop-types';
import { Header, PinnacleList } from 'components';
import { Layout, Container } from 'layouts';

const questionStyle = {
  "font-weight": "bold",
  "font-size": "1.5em",
  "margin-bottom": "1em",
  "margin-top": "1em"
};

const Faq = ({ data, center }) => {
  return (
    <Layout>
      <Helmet title={'FAQ'} />
      <Header title="Destiny Pinnacle Services">Frequently Asked Questions</Header>
    <Container center={center}>
      <div className="qAndA">
        <div className="question" style={questionStyle}>How does your service work?</div>
        <div className="answer">
          We offer 2 different types of services. See below for details:
          <ol>
            <li>
              The first is a recovery service. This means that we log onto your account and play for you. This is the cheapest
              option because it is easiest for us. We will work with you on times for us to play so that we do not interfere with your schedule.
            </li>
            <li>
              The second option is a carry service. This means you play on your own account and we play with you, "carrying" you to completion of the service. This option
              costs a little more because it is more difficult. However, the value you get out of this option cannot be overstated. There are many benefits to opting for a carry service rather 
              than a recovery service. 
              <ul>
                <li>You play on your account the entire time, meaning you truly earned your reward yourself and can say that with honesty.</li>
                <li>Our players are some of the best in the game and are also extremely friendly. They will guide you along the way and offer valuable game knowledge to improve your own gameplay.
                They will never talk down to you or blame anything on you.</li> 
                <li>They will teach you game sense, top tier strategies, and ways to improve your own K/D, while also offering advice on loadouts and classes to use for your best gameplay.</li>
                <li>You will get to see how top tier players work in real time competitive games.</li>
                <li>
                  Keep in mind that if you do choose the carry service, it is always possible you will lose a few games. This is not the end of the world. Carrying is difficult and you can be unlucky if you match very good players. 
                  However, if you pay for the service at the agreed upon price, we guarantee your service will be completed no matter how long it takes. Our boosters have over 90% win rates in the Competitive playlist, but anything can happen.
                </li>
              </ul>
            </li>
          </ol>
        </div>
      </div>
      <div className="qAndA">
        <div className="question" style={questionStyle}>How can I trust you with my account?</div>
        <div className="answer">
          At Destiny Pinnacle Services, we only employ a select few indivduals who we personally know and trust. Everyone who is a "booster" here is someone we have personally known for a good amount of time.
          Unlike at other sites, where they try to hire as many people as possible to churn out as many orders as possible, we take the opposite approach. We keep our team small and work hard to complete orders
          with the mindset of "Quality Over Quantity". I would personally trust my own account with any of the people on our team.
        </div>
      </div>
      <div className="qAndA">
        <div className="question" style={questionStyle}>Why should I choose DPS over another recov site?</div>
        <div className="answer">
          <ul>
            <li>
              Like I said above, we only employ people who we know and trust, and who are friendly. Other sites will employ anyone with good stats or a big following on Twitch. I have personally played against and watched Twitch streamers doing recovs
              and people doing recovs that work for other sites who will T-bag, emote, and even whisper people in game <strong>while on your account</strong> and talk shit to them and be toxic. They have even said things that could get your account banned. Our team is never toxic, will never bag or emote or message
              anyone while we are logged onto your account. The other sites don't care about your account, they care about your money. At DPS we will never do anything that could harm your account, your standing with Blizzard/Bungie/Steam or even make other players dislike you.
            </li>
            <li>
              <strong>All of our prices are negotiable.</strong> If you think our prices are not fair, contact us anyway and we will work with you. We truly enjoy helping people achieve something that would be very difficult for them to achieve on their own. That's also why we enjoy the carry
              service option so much.
            </li>
            <li>When ordering with DPS, you will speak to a real person via Discord soon after you place your order. Other sites will have you place an order and send your login details via email and you never actually talk to a real person. 
              We will work with you and update you along the way with our progress.</li>
            <li>
              Your account is secure with us. We will <strong>never</strong> have you send us your login details over email. Email is extremely insecure and any site that asks you to do this (most of the other sites) should not be trusted. Your account details could be stolen by anyone
              with a little technical ability. We will only get your login details through a private message with only 2 parties involved (you and one of our boosters). The other sites will pass your password around between all of their boosters and it could easily be stolen or given to someone with malicious intent.
            </li>
          </ul>
        </div>
      </div>
      <div className="qAndA">
        <div className="question" style={questionStyle}>How will I pay for an order?</div>
        <div className="answer">
          Once you place an order, you will be contacted by a DPS rep with a few hours (depending on how busy we are). You will speak to each other over Discord in a private message and work out any details about the service. Once both sides agree on all the details, you will use PayPal to pay our official DPS account. 
          Once we have received payment, the DPS rep has the green light to proceed with your order. You will <strong>always</strong> be able to directly contact the person who is taking care of your order in case you have any questions, and they will provide progress updates along the way.
        </div>
      </div>
      <div className="qAndA">
        <div className="question" style={questionStyle}>Will you carry me for a shoutout and exposure?</div>
        <div className="answer">
          Unfortunately shoutouts, exposure and Instagram followers do not pay the bills, so <strong>no, we will not carry you for a shoutout</strong>.
        </div>
      </div>
      <div className="qAndA">
        <div className="question" style={questionStyle}>How good are your boosters?</div>
        <div className="answer">
          Our boosters are in the top 0.5% of Destiny players for K/D and kills per game. They routinely finish each season with Diamond ELO in all playlists. They have all obtained each pinnacle weapon (including Redrix and Not Forgotten) to date legitamately. No win trading, no cheesing on Asia servers, just good ole fashioned winning. 
          They are all easily capable of soloing the competitive playlist to 2100 Glory rank and have carried many friends and clanmates to Fabled rank as well (even players with less than 0.5 K/D ratios).
          One of our boosters was a Crucible Sherpa who would coach people in game 1 on 1 while also performing carry services.
        </div>
      </div>
      <div className="qAndA"> 
        <div className="question" style={questionStyle}>What platforms do you offer services on?</div>
        <div className="answer">
          Currently we only offer services on PC. If the interest is large enough we may extend our services to console. Please <a href="/contact">contact us</a> and let us know you want a service on a console and we may be able to hook you up with a trusted booster.
        </div>
      </div>
      <div className="qAndA"> 
        <div className="question" style={questionStyle}>Why don't you offer a service for X weapon?</div>
        <div className="answer">
          If we don't have exactly what you're looking for, <a href="/contact">contact us anyway</a>. We specialize in PvP but can work with you on anything you need recovered in the game. If I can't do it myself, I know someone who can.
        </div>
      </div>
    </Container>
    </Layout>
  );
};

export default Faq;

Faq.propTypes = {
  data: PropTypes.shape({
    allMarkdownRemark: PropTypes.shape({
      edges: PropTypes.arrayOf(
        PropTypes.shape({
          node: PropTypes.shape({
            excerpt: PropTypes.string,
            frontmatter: PropTypes.shape({
              cover: PropTypes.object.isRequired,
              path: PropTypes.string.isRequired,
              title: PropTypes.string.isRequired,
              price: PropTypes.string.isRequired,
              sortOrder: PropTypes.number.isRequired,
              tags: PropTypes.array,
            }),
          }),
        }).isRequired
      ),
    }),
  }),
};

Container.propTypes = {
  center: PropTypes.object,
};

export const query = graphql`
  query {
    allMarkdownRemark(sort: { order: ASC, fields: [frontmatter___sortOrder] }) {
      edges {
        node {
          id
          excerpt(pruneLength: 200)
          frontmatter {
            title
            path
            tags
            price
            sortOrder
            cover {
              childImageSharp {
                fluid(
                  maxWidth: 1000
                  quality: 90
                  traceSVG: { color: "#2B2B2F" }
                ) {
                  ...GatsbyImageSharpFluid_withWebp_tracedSVG
                }
              }
            }
          }
        }
      }
    }
  }
`;
